import React from 'react'
import { GatsbyImage } from "gatsby-plugin-image"
import PostDetails from './PostDetails'

export default function PostHeader({frontmatter, timeToRead, showLang, showTags}) {

	return(
		<div>
			<div className="post-title">{frontmatter.title}</div>
			<div className="post-details-in-post">
				<PostDetails frontmatter={frontmatter} timeToRead={timeToRead} showLang={showLang} showTags={showTags} />
			</div>
			{frontmatter.showimage &&
				// <img
				// 	src={frontmatter.image.publicURL}
				// 	alt={frontmatter.title}
				// 	id="cover"
				// />
				<GatsbyImage image={frontmatter.image.childImageSharp.gatsbyImageData} />
			}
		</div>
	)
}